import React from "react";
import Square from "../../components/Square/index.js";
import * as variables from "./variables.js";
import "./index.css";

export default class Map extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      grid: Array(32).fill().map(() => Array(32).fill(0)),
      height: 32,
      width: 32
    };

    this.interval = null

    this.next = this.next.bind(this)
    this.onClick = this.onClick.bind(this)
    this.onPlay = this.onPlay.bind(this)
    this.onStop = this.onStop.bind(this)
  }

  calculate(i, j) {
    let count = 0

    // TOP
    if (i > 0) {
      count += this.state.grid[i-1][j]
      // TOP-LEFT
      if (j > 0) {
        count += this.state.grid[i-1][j-1]
      }
      // TOP-RIGHT
      if (j < this.state.width - 1) {
        count += this.state.grid[i-1][j+1]
      }
    }
    // LEFT
    if (j > 0) {
      count += this.state.grid[i][j-1]
    }
    // RIGHT
    if (j < this.state.width - 1) {
      count += this.state.grid[i][j+1]
    }
    // BOTTOM
    if (i < this.state.width - 1) {
      count += this.state.grid[i+1][j]
      // BOTTOM-LEFT
      if (i > 0) {
        count += this.state.grid[i+1][j-1]
      }
      // BOTTOM-RIGHT
      if (j < this.state.width - 1) {
        count += this.state.grid[i+1][j+1]
      }
    }

    return count
  }

  next() {
    var nextState = [];

    for (var i = 0; i < this.state.height; i++) {
      nextState[i] = this.state.grid[i].slice();
    }

    for (var i = 0; i < this.state.height; i++) {
      for (var j = 0; j < this.state.width; j++) {
        let adj_cells = this.calculate(i, j)

        if (this.state.grid[i][j] == 1) {
          if (adj_cells < 2 || adj_cells > 3) {
            nextState[i][j] = 0
          }
        }
        else {
          if (adj_cells == 3) {
            nextState[i][j] = 1
          }
        }
      }
    }

    this.setState({grid: nextState})
  }

  onClick(row, col) {
    let newGrid = this.state.grid

    if (this.state.grid[row][col] === 0) {
      newGrid[row][col] = 1
    }
    else {
      newGrid[row][col] = 0
    }

    this.setState({grid: newGrid})
  }

  onPlay() {
    if (this.interval != null) {
      clearInterval(this.interval);
    }
    this.interval = setInterval(this.next, 100);
  }

  onStop() {
    if (this.interval != null) {
      clearInterval(this.interval);
    }
  }

  render() {
    let map = []

    for (let i = 0; i < this.state.height; i++) {
      for (let j = 0; j < this.state.width; j++) {
        map.push(<Square value={this.state.grid[i][j]}
          row={i} col={j} onClick={this.onClick}/>)
      }
    }

    return (
      <div className="map">
        <div className="grid">
          {map}
        </div>
        <button onClick={this.next}>Next</button>
        <button onClick={this.onPlay}>Play</button>
        <button onClick={this.onStop}>Stop</button>
      </div>
    );
  }
}
